.loader-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh; /* tela cheia */
  background: #f9f9f9;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #555;
}

.loader {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  margin-bottom: 10px;
}

.loader div {
  position: absolute;
  border: 4px solid #007bff; /* cor azul */
  opacity: 1;
  border-radius: 50%;
  animation: loaderAnim 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}

.loader div:nth-child(2) {
  animation-delay: -0.5s;
}

.loader div:nth-child(3) {
  animation-delay: -0.25s;
}

@keyframes loaderAnim {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 0;
  }
  50% {
    width: 72px;
    height: 72px;
    top: 4px;
    left: 4px;
    opacity: 1;
  }
  100% {
    width: 0;
    height: 0;
    top: 36px;
    left: 36px;
    opacity: 0;
  }
}

.loader-wrapper p {
  font-size: 16px;
  font-weight: 500;
}
