/*
 * TRANSITION DO SUBMENU
 * Use esta seo para animar a abertura e fechamento dos submenus.
 */

.pe-slide-menu {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease-out, opacity 0.3s ease-out;
    opacity: 0;
}

.pe-slide-menu.show {
    max-height: 500px; /* Um valor alto para garantir que o menu completo aparea */
    opacity: 1;
}

/*
 * ROTAO DO CONE
 * Gira o cone de seta para indicar o estado do menu (aberto/fechado).
 */

.pe-has-sub .pe-nav-arrow {
    transition: transform 0.3s ease-out;
    transform: rotate(0deg);
}

.pe-has-sub .pe-nav-arrow.open {
    transform: rotate(-180deg);
}